     padding: 0.5rem;
    font-size: 0.8rem;
  }
  .header{
    padding: 0.5rem;
    font-size: 1rem;
  }

  .Carousel{
    background-position:center;
    background-size:auto;
    background-repeat: no-repeat;
    height: 30vh; 
    width: auto;
    margin: 0;
    margin-left: auto;
    flex-shrink: 2;
  }
  .main-content {
    flex-direction: column;
    padding: 0.5rem;
    gap: 0;
  }

  .filter-container {
    display: none; 
  }

  .filter-toggle {
    display: block;
    background-color: #f1f1f1;
    padding: 0.75rem 1rem;
    text-align: center;
    font-weight: 500;
    cursor: pointer;
    border-bottom: 1px solid #ddd;
  }

