    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
    gap: 1rem;
    padding: 0.5rem;
  }

  .no-products-message {
    width: 70%;
    margin: 2rem auto;
    font-size: 1rem;
    text-align: center;
  }
    .search-container {
    display: flex;
    align-items: center;
    background-color: #f1f3f6;
    padding: 0.5rem;
    border-radius: 20px;
    width: 70%;
    margin-left: auto;
  }

  .search-icon {
    font-size: 1.2rem;
    margin-right: 0.5rem;
    color: #555;
  }

  .search-bar {
    border: none;
    background: transparent;
    outline: none;
    width: 100%;  
    font-size: 1rem;
  }
  .button-44{
     padding: 4px 8px;
     font-size: 12px;
     border-radius: 4px;
  }
  .wish-card {
    flex-direction: column;
    align-items: center;
    width: 90%;
