  }

  .price {
    font-size: 0.9rem;
    text-align: center;
  }

  .button-32 {
    padding: 6px;
    font-size: 0.9rem;
    width: auto;
  }

  .wish-btn {
    position: static;
    margin: 0.5rem auto;
    width: auto;
    padding: 0.5rem;
    display: block;
    text-align: center;
  }

  .badge,
  .badge-wish {
    position: static;
    margin-top: 0.3rem;
    right: auto;
    top: auto;
    display: block;
  }

  .cart {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .cart-card {
    flex-direction: column;
    align-items: center;
    width: 20%;
    padding: 1rem;
  }

  .cart-img {
    width: 100%;
    height: auto;
    max-height: 160px;
    object-fit: cover;
    border-radius: 8px;
  }

  .cart-order {
    width: 90%;
    height: auto;
    padding: 1rem;
    margin: 1rem auto;
  }

  .empty-cart {
    width: 70%;
    display: block;
    margin: 2rem auto 1rem auto;
    object-fit: contain;
  }

  .empty-cart-text {
    text-align: center;
    font-weight: 900;
    color: var(--primary-color);
    background-color: var(--secondary-color);
    padding: 0.75rem;
    margin: 0 auto;
    border-radius: 50%;
    font-family: cursive;
    width: fit-content;
  }

  .cart-counter {
    right: 15px;
    top: 10px;
    padding: 4px 6px;
    font-size: 10px;
  }

  .btn-trash {
    width: 12%;
  }

  .button {
    padding: 8px 14px;
