  }
}



@media (min-width: 768px) {
  .container {
    padding: 1rem;
  }

  .heading {
    font-size: 1rem;
  }

  .card {
    max-width: 600px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 1000px;
    padding: 1rem;
    margin: 0 auto;
  }

  .heading {
    font-size: 3rem;
  }

  .card {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    gap: 3rem;
  }
 
}
